import logo from './logo.svg';
import './App.css';
import { render } from '@testing-library/react';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p 
        id = "p"
        onClick={color}>
          I have edited <code></code> my first component
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}



function color(){
  var randomColor = "#" + Math.floor(Math.random()*16777216).toString(16);
  document.getElementById("p").style.color = randomColor
  return randomColor;
}




export default App;
